              iconfont: "iconfont icon-shujuzhiliang",
              name: ""
            }
          ]
        }
      ],
      hoverActiveIndex: -1, // 
      clientWidth: document.body.clientWidth, // 
      nowPage: sessionStorage.getItem("activePage"), // 
      ActivePage: sessionStorage.getItem("activePage"), // sessionStroage 
      isCollapse: Boolean // 
    };
  },
  computed: {
    // SPAvuexvuexvuex
    // ActivePage() {
    //   return sessionStorage.getItem("activePage");
    // }
  },
  methods: {
    // 
    HoverSate(index) {
      this.hoverActiveIndex = index;
    },
    // 
    navRoute(navItem) {
      this.ActivePage = navItem.index;
      // 
      if (!navItem.child) {
        //  sessionStroage 
        sessionStorage.setItem("activePage", navItem.index);
        window.location.href = `${
          this.$baseUrl === "/" ? "/views/" : this.$baseUrl
        }${navItem.index}/index.html`;
      }
    }
  },
  created() {
    // DOMDOM
    if (this.clientWidth <= 1366) {
      this.isCollapse = true;
    } else {
      this.isCollapse = false;
    }
  },
  mounted() {
    // 
    window.addEventListener("resize", event => {
      this.clientWidth = document.body.clientWidth;
      // 
      if (this.clientWidth <= 1366) {
        this.isCollapse = true;
      } else {
        this.isCollapse = false;
      }
    });
  }
};
</script>

<style lang="scss" scoped>
// 
.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateX(-100%);
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s ease;
}
// 
.app-sidebar {
  display: inline-block;
  height: 100%;
  background-color: #333f4e;
  .sidebar-nav-item {
    .sidebar-nav-sub-menu,
    .sidebar-nav-child-menu {
      position: relative;
    }
    a {
      display: block;
      height: 40px;
      line-height: 40px;
      background-color: #333f4e;
      color: #fff;
    }
    .nav-content {
      white-space: nowrap;
    }
    .sidebar-nav-sub-menu {
      a.nav-item-icon {
        border-top: 1px solid #1a1b24;
        white-space: nowrap;
        .iconfont {
          font-size: 20px;
        }
      }
    }
    .sidebar-nav-child-menu {
      a.nav-item-icon {
        color: #8b9ab7;
      }
      a.active,
      a.nav-item-icon:hover {
        color: #fff;
      }
    }
  }
  .sidebar-nav-item:last-of-type {
    .sidebar-nav-sub-menu {
      a.nav-item-icon {
        border-bottom: 1px solid #1a1b24;
      }
    }
  }
  .sidebar-nav-list {
    transition: width 0.2s ease;
  }
  // 
  .sidebar-nav-list:not(.collapse-sidebar) {
    width: 200px;
    .nav-item-icon {
      color: #8b9ab7;
      .iconfont {
        display: inline-block;
        padding: 0 10px;
      }
    }
    .nav-item-icon.active {
      color: #fff;
    }
    .sidebar-nav-sub-menu {
      .nav-item-icon {
        i.iconfont {
          font-size: 14px;
          transition: all 0.2s ease;
        }
      }
      .nav-item-icon.active {
        i.iconfont {
          transform: rotateZ(-90deg);
        }
      }
    }
    .sidebar-nav-child-menu {
      a {
        height: 30px;
        line-height: 30px;
        padding-left: 30px;
        font-size: 12px;
      }
      .nav-item-icon.active {
        padding-left: 24px;
        border-left: 6px solid #f8a900;
        color: #fff;
        background-color: #1a1b24;
      }
    }
  }
  // 
  .collapse-sidebar {
    width: 50px;
    .nav-item-icon {
      z-index: 99;
      position: relative;
      text-align: center;
    }
    .nav-item-icon.active {
      background-color: #1a1b24;
    }
    a.nav-item-tip {
      z-index: 98;
      position: absolute;
      top: 0;
      left: 50px;
      padding-right: 10px;
      white-space: nowrap;
      text-align: left;
      background-color: #333f4e;
    }
    .sidebar-nav-sub-menu {
      .nav-item-icon.active {
        border-left: 6px solid #f8a900;
        background-color: #1a1b24;
      }
    }
  }
}
</style>
